<template>
<div>
    <el-button type="danger" @click="del">删除</el-button>
</div>
</template>
<script>
 import {mapGetters,mapActions} from 'vuex';
export default {
props: [],
components: {},
data() {
return {};
},
computed: {
...mapGetters({}),
},
methods: {
...mapActions({}),
del(){
    this.$confirm('是否确认删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.$emit('del')
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          }); 
        });
}
},
mounted() {},
};
</script>
<style scoped>
</style>


