import Vue from 'vue'
import Vuex from 'vuex'
import {reqMenuList } from '../../utils/request'
Vue.use(Vuex)

let state={
    menuList:[],
}
let mutations={
    changeMenuList(state,arr){
        state.menuList=arr
    },
}
let actions={
    getMenuList(context){
        reqMenuList().then(res=>{
            context.commit('changeMenuList',res.data.list)
        })
    }
}
let getters={
    menuList(state){
        return state.menuList
    }
}



export default {
    state,
    mutations,
    actions,
    getters
}


