import Vue from 'vue'
import Vuex from 'vuex'
import {memberlist } from '../../utils/request'
Vue.use(Vuex)

let state={
    member:[],
}
let mutations={
    changememberList(state,arr){
        state.member=arr
    },
}
let actions={
    getmemberList(context){
        memberlist().then(res=>{
            context.commit('changememberList',res.data.list)
        })
    }
}
let getters={
    member(state){
        return state.member
    }
}



export default {
    state,
    mutations,
    actions,
    getters
}


