import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/index'


Vue.use(Router)
export const aa=function(path,next){
    if(store.state.userinfo.menus_url.includes(path)){
      next()
    }else{
      console.log(11111);
      next('/login')
    }
}
export const indexRouters = [{
  path: "menu",
  name:"菜单管理",
  component: () => import("../page/menu/menu.vue"),
  beforeEnter: (to, from, next) => {
    
      aa('/menu',next)
  }
},
{
  path: "role",
  name:"角色管理",
  component: () => import("../page/role/role.vue"),
  beforeEnter: (to, from, next) => {
    aa('/role',next)
}
},
{
  path: "manage",
  name:"管理员管理",
  component: () => import("../page/manage/manage.vue"),
  beforeEnter: (to, from, next) => {
    aa('/manage',next)
}
},
{
  path: "cate",
  name:"商品分类",
  component: () => import("../page/cate/cate.vue"),
  beforeEnter: (to, from, next) => {
    aa('/cate',next)
}
},
{
  path: "spec",
  name:"商品规格",
  component: () => import("../page/spec/spec.vue"),
  beforeEnter: (to, from, next) => {
    aa('/spec',next)
}
},
{
  path: "goods",
  name:"商品管理",
  component: () => import("../page/goods/goods.vue"),
  beforeEnter: (to, from, next) => {
    aa('/goods',next)
}
},
{
  path: "member",
  name:"会员管理",
  component: () => import("../page/member/member.vue"),
  beforeEnter: (to, from, next) => {
    aa('/member',next)
}
},
{
  path: "banner",
  name:"轮播图管理",
  component: () => import("../page/banner/banner.vue"),
  beforeEnter: (to, from, next) => {
    aa('/banner',next)
}
},
{
  path: "seckill",
  name:"秒杀活动",
  component: () => import("../page/seckill/seckill.vue"),
  beforeEnter: (to, from, next) => {
    aa('/seckill',next)
}
}
]
const router = new Router({
  routes: [
      {
        path:"/login",
        component:()=>import('../page/login/login.vue'),
      },
      {
        path:"/index",
        component:()=>import('../page/index/index.vue'),
        children:[
          ...indexRouters,
          {
            path:"",
            component:()=>import('../page/home/home.vue')
          }
        ]
      }
  ]
})

router.beforeEach((to, from, next) => {
  // console.log(to);
  // 如果是去登录页，直接进入
  if(to.path=='/login'){
    next()
    return
  }
  // 如果携带的有token，则进入对应页面，否则进入登录页
  if (store.state.userinfo.token) {
    next();
  }else{
    next("/login")
  }
})


export default router


