// ===============商品分类==================
// 商品分类添加
export const cateadd=(form)=>{
    let data=new FormData()
    for(let i in form ){
        data.append(i,form[i])
    }
    return axios({
        url:baseUrl+"/api/cateadd",
        method:"post",
        data:data
    })
}

