import Vue from 'vue'
import Vuex from 'vuex'
import {reqRoleList } from '../../utils/request'
Vue.use(Vuex)

let state={
    roleList:[],
}
let mutations={
    changeRoleList(state,arr){
        state.roleList=arr
    },
}
let actions={
    getRoleList(context){
        reqRoleList().then(res=>{
            context.commit('changeRoleList',res.data.list)
        })
    }
}
let getters={
    roleList(state){
        return state.roleList
    }
}



export default {
    state,
    mutations,
    actions,
    getters
}


