import Vue from 'vue'
import Vuex from 'vuex'
import {catelist} from '../../utils/request'
Vue.use(Vuex)

let state={
    cateList:[]
}
let mutations={
    changecateList(state,arr){
        state.cateList=arr
    }
}
let actions={
    getcateList(context){
        catelist({istree:true}).then(res=>{
            context.commit('changecateList',res.data.list)
        })
    }
}
let getters={
    cateList(state){
        return state.cateList
    }
}



export default {
    state,
    mutations,
    actions,
    getters,
}


