import Vue from 'vue'
import Vuex from 'vuex'
import {specslist,specsTotal} from '../../utils/request'
Vue.use(Vuex)

let state={
    specsList:[],
    specstotal:0,
    specssize:1,
    specspage:1
}
let mutations={
    changespecsList(state,arr){
        state.specsList=arr
    },
    changetotal(state,num){
        state.specstotal=num
    },
    changespecspage(state,num){
        state.specspage=num
    }
}
let actions={
    getspecsList(context,bool){
        let params=bool?'':{size:context.state.specssize,page:context.state.specspage}
        specslist(params).then(res=>{
            let list=res.data.list?res.data.list:[]
            if(list.length==0&&context.state.specspage>1){
                context.commit('changespecspage',context.state.specspage-1)
                context.dispatch('getspecsList')
                return
            }
            context.commit('changespecsList',list)
        })
    },
    specsTotal1(context){
        specsTotal().then(res=>{
            context.commit('changetotal',res.data.list[0].total)
        })
    },
    reqchangespecspage(context,num){
        context.commit('changespecspage',num)
        context.dispatch('getspecsList')
    }
}
let getters={
    specsList(state){
        // state.specsList.forEach(item=>{
        //     item.attrs = JSON.parse(item.attrs)
        // })
        return state.specsList
    },
    specstotal(state){
        return state.specstotal
    },
    specssize(state){
        return state.specssize
    }
}



export default {
    state,
    mutations,
    actions,
    getters,
}


