import Vue from 'vue'
import Vuex from 'vuex'
import {reqgoodslist,goodstotal} from '../../utils/request'
Vue.use(Vuex)

let state={
    goodslist:[],
    goodspage:1,
    goodssize:2,
    goodstotal:0,
}
let mutations={
    changegoodslist(state,arr){
        state.goodslist=arr
    },
    changegoodstotal(state,num){
        state.goodstotal=num
    },
    changegoodspage(state,num){
        state.goodspage=num
    }
}
let actions={
    getgoodslist(context,params){
        params=params?params:{size:context.state.goodssize,page:context.state.goodspage}
        reqgoodslist(params).then(res=>{
            let list=res.data.list?res.data.list:[]
                if(list.length==0&&context.state.goodspage>1){
                    context.commit('changegoodspage',context.state.goodspage-1)
                    context.dispatch('getgoodslist')
                    return
                }
            context.commit('changegoodslist',list)
        })
    },
    goodsTotal1(context){
        goodstotal().then(res=>{
            context.commit('changegoodstotal',res.data.list[0].total)
        })
    },
    reqchangegoodspage(context,num){
        context.commit('changegoodspage',num)
        context.dispatch('getgoodslist')
    }
}
let getters={
    goodslist(state){
        return state.goodslist
    },
    goodstotal(state){
        return state.goodstotal
    },
    goodssize(state){
        return state.goodssize
    }
}



export default {
    state,
    mutations,
    actions,
    getters,
}


