import Vue from 'vue'
import Vuex from 'vuex'
import {secklist} from '../../utils/request'
Vue.use(Vuex)

let state={
    seckilllist:[],
}
let mutations={
    changeseckList(state,arr){
        state.seckilllist=arr
    },
}
let actions={
    getseckList(context){
        secklist().then(res=>{
            context.commit('changeseckList',res.data.list)
        })
    }
}
let getters={
    seckilllist(state){
        return state.seckilllist
    }
}



export default {
    state,
    mutations,
    actions,
    getters
}


