import Vue from 'vue'
import Vuex from 'vuex'
import {bannerlist} from '../../utils/request'
Vue.use(Vuex)

let state={
    bannerList:[]
}
let mutations={
    changebannerList(state,arr){
        state.bannerList=arr
    }
}
let actions={
    getbannerList(context){
        bannerlist().then(res=>{
            context.commit('changebannerList',res.data.list)
        })
    }
}
let getters={
    bannerList(state){
        return state.bannerList
    }
}



export default {
    state,
    mutations,
    actions,
    getters,
}


