import Vue from 'vue'
import Vuex from 'vuex'
import {requserList,reqTotal} from '../../utils/request'
Vue.use(Vuex)

let state={
    userList:[],
    total:0,
    size:1,
    page:1
}
let mutations={
    changeuserList(state,arr){
        state.userList=arr
    },
    changetotal(state,num){
        state.total=num
    },
    changepage(state,num){
        state.page=num
    }
}
let actions={
    getuserList(context){
        requserList({size:context.state.size,page:context.state.page}).then(res=>{
            let list=res.data.list?res.data.list:[]
            if(list.length==0&&context.state.page>1){
                context.commit('changepage',context.state.page-1)
                context.dispatch('getuserList')
                return
            }
            context.commit('changeuserList',list)
        })
    },
    reqTotal1(context){
        reqTotal().then(res=>{
            context.commit('changetotal',res.data.list[0].total)
        })
    },
    reqchangepage(context,num){
        context.commit('changepage',num)
        context.dispatch('getuserList')
    }
}
let getters={
    userList(state){
        return state.userList
    },
    total(state){
        return state.total
    },
    size(state){
        return state.size
    }
}



export default {
    state,
    mutations,
    actions,
    getters,
}


